async function fetchDashboardAnalyticFactory({commit}) {
    return await this.$axios
        .get('/dashboard-analytic-factory')
        .then((res) => {
            if (res && res.data) {
                commit("SET_DASHBOARD_ANALYTIC_FACTORY", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}







export default {
    fetchDashboardAnalyticFactory
};
