async function getFilteredData({commit}, payload) {
    return await this.$axios.get(payload.routeURL + '?search=' + payload.searchValue, )
        .then((res) => {
            if (res && res.data) {
            }
            return (res && res.data) || {}
        }).catch((error) => {
            return error
        })
}


async function postSignIn({commit}, payload) {
    return await this.$axios({
        method: "POST",
        url: '/login',
        data: {
          email: payload.email,
          password: payload.password
        },
      })
      .then((e) => {
        commit('SET_USER_TO_STATE', e.data);
        sessionStorage.setItem('user', JSON.stringify(e.data));
        // payload.router.push({path: '/main'});
        return e;
      })
      .catch((error) => {
        return error;
      })


      // //  Used when above code not works
      // const data = {token:"Token 9825cabf4345b6abdce62e02ff082cc977c7a9b0",user_id:1,business_id:1}
      // commit('SET_USER_TO_STATE', data);
      // sessionStorage.setItem('user', JSON.stringify(data));
      // payload.router.push({path: '/main'})
  }

 
function stateChanged({commit}, payload){
    commit('SET_USER_TO_STATE', payload);
}


function logOut({commit}){
  commit('LOG_OUT');
}



export default { getFilteredData, postSignIn, stateChanged, logOut };
