async function fetchAllExpenses({commit}, payload) {
    return await this.$axios
        .get('/expense-payments', payload)
        .then((res) => {
            if (res && res.data) {
                commit("SET_EXPENSES", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

