async function fetchReadyOrderIncome({commit}, params) {
    return await this.$axios
        .get('/ready-order-income', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_READY_ORDER_INCOME", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

async function fetchSales({commit}, params) {
    return await this.$axios
        .get('/sales', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_SALES", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

async function postSales({commit}, payload) {
    return await this.$axios
        .post('/sales', payload)
        .then((res) => {

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

async function fetchStorageReadyProduct({commit}, params) {
    return await this.$axios
        .get('/storage-ready-product', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_STORAGE_READY_PRODUCT", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}



export default {fetchReadyOrderIncome, fetchSales, postSales, fetchStorageReadyProduct};
