import axios from "axios";
import router from '../router/index'

axios.defaults.withCredentials = true;


// let axiosParams = {
//     baseURL: "https://mountain-it.tech/api",
//     // baseURL: "http://127.0.0.1:8000/api",
//     timeout: 60000,
// }


// if(!user){
//     axiosParams.headers = {"X-Custom-Header": "foobar", "Accept": "application/json"}
// }else {
//     axiosParams.headers = {"X-Custom-Header": "foobar", "Accept": "application/json", "Authorization": "Bearer " + user.token}
// }

const instance = axios.create({
    baseURL: "https://mountain-it.tech/api",
    // baseURL: "http://127.0.0.1:8000/api",
    timeout: 60000,
    headers: {"X-Custom-Header": "foobar", "Accept": "application/json"}
});


//send token
instance.interceptors.request.use(
    function (config) {
        let user = JSON.parse(sessionStorage.getItem('user'));
        console.log(user)
        console.log(config);

        if(user && user.token){
            config.headers.Authorization = "Bearer " + user.token
        }
        
        return config;
    },
    function (error) {
        return Promise.reject(error);
    },
);


//404 or 401 pages use this response
instance.interceptors.response.use(
    function (response) {
        return response;
    },
    function (error) {
        if(error.response.status == '401'){
            sessionStorage.removeItem('user');
            router.push({name: 'login'})
        }
        return Promise.reject(error);
    },
);

export default instance;
