import { extend } from 'vee-validate';
import { required, email } from 'vee-validate/dist/rules';

// No message specified.
extend('email', email);

// Override the default message.
extend('required', {
  ...required,
  message: 'Ushbu maydon to`ldirilishi shart!'

});

extend('error', (value, args) => {
  if (args[0] == true) {

    if(args[2] == args[3]){
      return args[1];
    }

    return true

  }else{

    return true
  
  }
});
