// =========== IP related apis ===========
async function postMatoOrder({commit}, payload) {
    return await this.$axios
        .post('/order', payload)
        .then((res) => {
            if (res && res.data) {
                commit("SET_MATO_ORDER", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}
async function fetchOrders({commit}, params) {
    return await this.$axios
        .get('/order', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_ORDERS", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}
