async function fetchFactorySewingHistory({commit}, params) {
    return await this.$axios
        .get('/factory-sewing-history', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_FACTORY_SEWING_HISTORY", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}


