/* eslint-disable */
async function fetchIncome({commit}, params, payload) {
    return await this.$axios
        .get('/product-income-thread', {params}, {payload})
        .then((res) => {
            if (res && res.data) {
                commit("SET_INCOME_IPS", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

