async function fetchAccessory({commit}, params) {
    return await this.$axios
        .get('/product-income-accessory', {params})
        .then((res) => {
            if (res && res.data) {
                commit("SET_ACCESSORY", res.data);
            }

            return (res && res.data) || {};
        }).catch((error) => {
            return error
        })
}

